.App {
  width: 100vw;
  max-height: 100vh;
  overflow-x: auto;
  overflow-y: hidden;
}
body{
  
  height: 100%;
  width: 100%;

}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media(max-height: 369px){
  .App{ overflow-y: auto;}
}
.first{
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('./wall.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size:cover;
  background-color: transparent;
  height: 100vh;
  width: 100%;
}
.second{
  background-color: #fefae0;
  height: 100vh;
  width: 100%;

  display:flex;
  justify-content:space-evenly;
  align-items:center;
}
.third{
  background-color: #fefae0;
  height: 100vh;
  width: 100%;

  display:flex;
  justify-content:space-evenly;
  align-items:center;
  flex-direction: row-reverse;
}
.fourth{
  background-color: #ddbea9;
  height: 100vh;
  width: 100%;

  display:flex;
  justify-content:center;
  flex-direction: column;
  align-items:center;
}
.top{
  width: 100%;
  height: 15vh;
  overflow: hidden;
  display: flex;
  align-items:center;
  justify-content: center;
color: white;

}
.bottom{
  width: 100%;
  height: 50vh;
  overflow: hidden;
  display: flex;
  align-items:center;
  justify-content: center;
  background-color: #b08968;
  color: black;
  
}
.start{
  animation: slow 5s ease forwards;
  overflow: hidden;
  text-shadow: 2px 2px #000;
  font-family: 'Scheherazade New', serif;
}
.aside{
  width: 100%;
  height: 100%;
  /* min-width: 1260px; */
  min-height: 128px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.header-container{
  display:flex;
  justify-content: space-between;
  align-items: center;
  background:transparent;
  height: 100%;
  border-bottom: solid;
  color: white;
  width: 100%;
}
.footer-container{
  display:flex;
  justify-content: space-between;
  align-items: center;
  background:transparent;
  height: 100%;
  border-bottom: solid;
  mix-blend-mode: normal;
  width: 60%;
}
@media screen and (max-width: 900px) {
  .footer-container{
    flex-direction: column;
  }
  .second{
    flex-direction: column;
  }
  .third{
    flex-direction: column;
  }
}
.homepage-container{
  display: flex;
  background: none;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  height:100%;
  width: 100%;
}
.contact-container{
  display: flex;
  background: none;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  height:100%;
  width: 100%;
  background: transparent;
}
ul li {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
}
ul{
  padding: 0;
  list-style-type: none;
}
@keyframes slow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}